import { latexCategory, type LatexItem, type LatexCategoryItem } from '../constants/latexList';
import RichText from 'simple-mind-map/src/plugins/RichText';
import 'quill/dist/quill.snow.css';
import katex from 'katex';
import { createElement } from '@/utils/html_utils';

interface LatexAutoPanelOptItem extends LatexItem {
    id: number;
    category: string;
    node: HTMLLIElement | null;
}
interface LatexAutoPanelOpt extends LatexCategoryItem {
    items: LatexAutoPanelOptItem[];
    node: HTMLLIElement | null;
}
const CSS_NAME = {
    LATEX_PANEL: '.latex-auto-panel',
    LATEX_PANEL_CATEGORY: '.latex-auto-panel-category',
    LATEX_PANEL_LST: '.latex-auto-panel-lst'
};
class RichTextEx extends RichText {
    private latexAuto = { show: false, focusLeft: false, word: '' };
    private latexItems: Map<number, LatexAutoPanelOptItem> = new Map();
    private latexCategory: Map<string, LatexAutoPanelOpt> = new Map();
    private showGroup: HTMLUListElement = document.createElement('ul');
    declare quill: any;
    declare styleEl: HTMLStyleElement;
    declare static instanceName: string;
    constructor(opt: { mindMap: any; pluginOpt: any }) {
        super(opt);
        this.initLatexAutoPanelOptItems();
    }
    initLatexAutoPanelOptItems() {
        let uid = 0;
        for (const k in latexCategory) {
            const lis = latexCategory[k].items
                .map((item, index) => ({ id: uid + index, ...item, category: k, node: null }))
                .map((x) => ({ ...x, node: this.createListItem(x) }))
                .toSorted((a, b) => (a.sort ?? 0) - (b.sort ?? 0));
            const li = document.createElement('li');
            li.setAttribute('data-value', k);
            li.innerHTML = `<span>${latexCategory[k].title}</span>`;
            this.latexCategory.set(k, {
                ...latexCategory[k],
                items: lis,
                node: li
            });
            uid += latexCategory[k].items.length;
            this.latexCategory.get(k)!.items.forEach((x) => this.latexItems.set(x.id, x));
        }
        const all = this.latexCategory.get('All')!;
        all.items.push(...this.latexItems.values());
        all.node?.classList.add('active');
    }
    initQuillEditor() {
        super.initQuillEditor();
        this.quill.on('text-change', (e: any) => {
            // 根据输入字符自动补全公式
            if (e?.ops?.length > 1)
                if (e?.ops?.[1]?.delete > 0 && this.latexAuto.word.length > 0) this.latexAuto.word = this.latexAuto.word.slice(0, -e?.ops?.[1]?.delete);
                else if (typeof e?.ops?.[1]?.insert === 'string') {
                    const le = e.ops[1].insert.trim();
                    if (le === '') this.latexAuto.word = '';
                    else if (le === '\\') this.latexAuto.word = '\\';
                    else if (this.latexAuto.word.length > 0) this.latexAuto.word += le;
                } else this.latexAuto.word = '';
            this.setLatexAutoPanelContent();
        });
        // 上下箭头选择上一个公式
        this.quill.keyboard.addBinding({ key: [38, 40] }, (_: any, { event: { code: c } }: any) => {
            if (this.latexAuto.show === false) return true;
            const div = this.getLatexAutoPanel();
            let subDiv: HTMLElement | null = null;
            if (this.latexAuto.focusLeft) subDiv = div.querySelector(CSS_NAME.LATEX_PANEL_CATEGORY);
            else subDiv = this.showGroup;
            if (subDiv == null) throw new Error('subDiv not found');
            const li = subDiv?.querySelector('li.active');
            if (li == null) subDiv?.querySelector('li:not(.hidden)')?.classList.add('active');
            else {
                if (c === 'ArrowUp') {
                    let prev = li.previousElementSibling;
                    while (prev && prev.classList.contains('hidden')) {
                        prev = prev.previousElementSibling;
                    }
                    if (prev == null) {
                        const arr = subDiv?.querySelectorAll(`li:not(.hidden)`) ?? [];
                        if (arr.length > 0) prev = arr[arr.length - 1];
                    }
                    prev?.classList.add('active');
                } else {
                    const nextSibling = li.parentElement?.querySelector('li.active ~ li:not(.hidden)');
                    if (nextSibling) nextSibling.classList.add('active');
                    else subDiv?.querySelector('li:not(.hidden)')?.classList.add('active');
                }
                li.classList.remove('active');
            }
            subDiv?.querySelector(`li.active`)?.scrollIntoView({
                block: 'nearest',
                inline: 'nearest'
            });
            if (this.latexAuto.focusLeft) {
                this.latexCategory.get(this.currentCategory)?.node?.classList.remove('focus');
                this.setLatexAutoPanelContent();
            }
        });
        // 左右箭头选择公式分类
        this.quill.keyboard.addBinding({ key: [37, 39] }, (_: any, { event: { code: c } }: any) => {
            if (this.latexAuto.show === false) return true; // 默认行为
            if ((c === 'ArrowRight' && this.latexAuto.focusLeft) || (c === 'ArrowLeft' && !this.latexAuto.focusLeft)) {
                const div = this.getLatexAutoPanel();
                this.latexAuto.focusLeft = c === 'ArrowLeft';
                div.querySelector(!this.latexAuto.focusLeft ? CSS_NAME.LATEX_PANEL_CATEGORY : CSS_NAME.LATEX_PANEL_LST)?.classList.remove('focus');
                div.querySelector(this.latexAuto.focusLeft ? CSS_NAME.LATEX_PANEL_CATEGORY : CSS_NAME.LATEX_PANEL_LST)?.classList.add('focus');
            }
        });
        // Esc 取消公式自动补全
        this.quill.keyboard.addBinding({ key: 27 }, () => {
            if (this.latexAuto.show === false) return true; // 默认行为
            this.hideLatexAutoPanel();
        });
        // 空格确定输入公式
        this.quill.keyboard.addBinding({ key: 32 }, (e: any) => {
            if (this.latexAuto.show === false) return true; // 默认行为
            const itm = this.latexCategory.get(this.currentCategory)?.items.find((x) => x.node?.classList.contains('active'));
            if (itm == null) return console.error('not found active item');
            itm.sort = (itm.sort ?? 0) + 1;
            const cmd = itm.cmd;
            const w = this.latexAuto.word;
            const sidx = e.index - w.length;
            this.quill.deleteText(sidx, w.length);
            this.quill.insertText(sidx, cmd, 'api');
            const i = itm.pos ? itm.pos - 1 : cmd.search(/(?<=\{.*?)\s+?(?=.*?\})|\{\}/g);
            if (i > -1) this.quill.setSelection(sidx + i + 1);
            this.hideLatexAutoPanel();
        });
        // 输入 $ 自动补全成对
        this.quill.keyboard.addBinding({ key: '$', shiftKey: true }, (e: { index: number; length: number }) => {
            if (e.length > 0) this.quill.deleteText(e.index, e.length);
            this.quill.insertText(e.index, '$$', 'api');
            this.quill.setSelection(e.index + 1);
        });
    }
    appendCss() {
        super.appendCss();
        const cssText = `          
            ${CSS_NAME.LATEX_PANEL}{
                display:flex;
                flex-direction:row;
                position:fixed;
                margin-top:10px;
                color:grey;
                border-radius:5px;
                box-shadow: rgba(0, 0, 0, 0.2) 4px 4px 4px 1px;
                background:white;
            }
            ${CSS_NAME.LATEX_PANEL} .hidden,${CSS_NAME.LATEX_PANEL}.hidden{
                display:none !important;
            }
            ${CSS_NAME.LATEX_PANEL} ul{
                width:100%;
                padding:0;
                margin:0;
            }
            ${CSS_NAME.LATEX_PANEL} ul li{
                height:36px;
            }
            ${CSS_NAME.LATEX_PANEL} ${CSS_NAME.LATEX_PANEL_LST},
            ${CSS_NAME.LATEX_PANEL} ${CSS_NAME.LATEX_PANEL_CATEGORY}{
                overflow-y:auto;
                scrollbar-width: none;
                min-width:180px;
                max-height:220px;
            }
            ${CSS_NAME.LATEX_PANEL} ${CSS_NAME.LATEX_PANEL_CATEGORY}{
                float:left;
                display:flex;
                background:#e2ffff;
                min-width:120px;
            }
            ${CSS_NAME.LATEX_PANEL} ${CSS_NAME.LATEX_PANEL_LST}::-webkit-scrollbar{
                display: none;
            }
            ${CSS_NAME.LATEX_PANEL} ${CSS_NAME.LATEX_PANEL_LST} li,
            ${CSS_NAME.LATEX_PANEL} ${CSS_NAME.LATEX_PANEL_CATEGORY} li {
                display: flex;
                justify-content: space-between;
                padding: 6px 12px;
                padding-left: 16px;
                list-style-type:none;
            }
            ${CSS_NAME.LATEX_PANEL} ${CSS_NAME.LATEX_PANEL_CATEGORY} span{
                margin:auto;
            }
            ${CSS_NAME.LATEX_PANEL} ${CSS_NAME.LATEX_PANEL_LST} li.active,
            ${CSS_NAME.LATEX_PANEL} ${CSS_NAME.LATEX_PANEL_CATEGORY} li.active{
                background:#85dbdb;
                font-weight: bold;
                color:white;
            }
            ${CSS_NAME.LATEX_PANEL} .focus .active{
                background:lightcoral !important;
            }
            ${CSS_NAME.LATEX_PANEL} .icon svg{
                width:100%;
                height:100%;
            }
        `;
        this.styleEl = document.createElement('style');
        this.styleEl.type = 'text/css';
        this.styleEl.innerHTML = cssText;
        document.head.appendChild(this.styleEl);
    }

    // 查找当前分类
    get currentCategory() {
        return this.getLatexAutoPanel().querySelector(`${CSS_NAME.LATEX_PANEL_CATEGORY} li.active`)?.getAttribute('data-value') ?? 'All';
    }

    // 隐藏公式自动补全控件
    hideLatexAutoPanel() {
        document.querySelector(CSS_NAME.LATEX_PANEL)?.classList.add('hidden');
        this.latexAuto.word = '';
        this.latexAuto.show = false;
        this.showGroup.innerHTML = '';
    }

    // 渲染公式自动补全面板
    renderLatexAutoPanel() {
        this.latexAuto.show = true;
        this.getLatexAutoPanel().classList.remove('hidden'); // 显示面板
        // 激活项保持在可视范围内
        this.latexCategory
            .get(this.currentCategory)
            ?.items.find((x) => x.node?.classList.contains('active'))
            ?.node?.scrollIntoView({
                block: 'nearest',
                inline: 'nearest'
            });
    }

    // 查询latex公式自动补全
    setLatexAutoPanelContent() {
        if (this.latexAuto.word.length < 1) return this.hideLatexAutoPanel();
        const k = this.latexAuto.word.substring(1).toLocaleLowerCase();
        for (const x of Array.from(this.showGroup.children)) x.classList.remove('active');
        this.showGroup.innerHTML = '';
        const lst = this.latexCategory.get(this.currentCategory)!.items;
        const temp1: LatexAutoPanelOptItem[] = [];
        const temp2: LatexAutoPanelOptItem[] = [];
        for (const x of lst) {
            if (x.cmd.toLocaleLowerCase().startsWith('\\' + k)) temp1.push(x);
            else if (x.tag.toLocaleLowerCase().startsWith(k)) temp1.push(x);
            else if (x.tag.toLocaleLowerCase().indexOf(k) > -1) temp2.push(x);
            else if (x.cmd.toLocaleLowerCase().indexOf(k) > -1) temp2.push(x);
        }
        const temp = [...temp1.sort((b, a) => (a.sort ?? 0) - (b.sort ?? 0)), ...temp2.sort((b, a) => (a.sort ?? 0) - (b.sort ?? 0))];
        temp.forEach((x) => {
            x.node?.classList.remove('active');
            this.showGroup.appendChild(x.node!);
        });
        temp[0].node!.classList.add('active');
        this.renderLatexAutoPanel();
    }

    // 创建公式自动补全面板列表项
    createListItem(itm: LatexAutoPanelOptItem) {
        const li = createElement('li', {
            attributes: { 'data-id': `${itm.id}`, 'data-value': itm.tag },
            innerHTML: `<span>${itm.tag}</span>`
        });
        const span = createElement('span', { classNames: ['icon'] });
        if (itm.icon && itm.icon.startsWith('<svg')) span.innerHTML = itm.icon;
        else if (itm.example) katex.render(itm.example, span, { displayMode: true });
        else if (itm.icon) span.innerHTML = itm.icon;
        li.appendChild(span);
        return li;
    }

    // 添加公式自动补全面板
    getLatexAutoPanel() {
        let div: HTMLElement | null = document.querySelector(CSS_NAME.LATEX_PANEL);
        if (div !== null) return div;
        div = document.createElement('div');
        div.classList.add('hidden', CSS_NAME.LATEX_PANEL.substring(1));
        const categoryHTML = `
            <div class="${CSS_NAME.LATEX_PANEL_CATEGORY.substring(1)} ${this.latexAuto.focusLeft ? 'focus' : ''}">
                <ul style="padding:0;margin:0;">
                    ${[...this.latexCategory.entries()].map(([, v]) => v.node?.outerHTML ?? '').join('')}
                </ul>
            </div>
        `;
        const listHTML = `<div class="${CSS_NAME.LATEX_PANEL_LST.substring(1)} ${this.latexAuto.focusLeft ? '' : 'focus'}"></div>`;
        div.innerHTML = categoryHTML + listHTML;
        document?.querySelector('.ql-editor')?.parentNode?.appendChild(div);
        const gdiv = div.querySelector(CSS_NAME.LATEX_PANEL_LST);
        gdiv?.appendChild(this.showGroup);
        const ul = document.createElement('ul');
        ul.classList.add('hidden', 'hidden-group');
        gdiv!.appendChild(ul);
        return div;
    }
}

RichTextEx.instanceName = 'richText';

export default RichTextEx;
