<script setup lang="ts">
import { onMounted, ref } from 'vue';
import MindMap from 'simple-mind-map';
import RichTextEx from '@/plugins/RichTextEx';
import FormulaEx from '@/plugins/FormulaEx';
import RichTextToolbar from './components/RichTextToolbar.vue';
import SvgContextMenu from '@/components/SvgContextMenu.vue';
import MenuToolbar from './components/MenuToolbar.vue';
import Export from 'simple-mind-map/src/plugins/Export.js';
import Select from 'simple-mind-map/src/plugins/Select.js';
import Drag from 'simple-mind-map/src/plugins/Drag.js';
import NodeBase64ImageStorage from 'simple-mind-map/src/plugins/NodeBase64ImageStorage.js';

import { storeToRefs } from 'pinia';
import { useMainStore } from '@/stores/main';
import { useStateStore } from '@/stores/state';
import { useSettingsStore } from '@/stores/settings';
const { loading } = storeToRefs(useStateStore());
const store = useMainStore();
const settingsStore = useSettingsStore();

RichTextEx.instanceName = 'richText';
MindMap.usePlugin(RichTextEx);
MindMap.usePlugin(FormulaEx);
MindMap.usePlugin(Export);
MindMap.usePlugin(Select);
MindMap.usePlugin(Drag);
MindMap.usePlugin(NodeBase64ImageStorage);

const inited = ref(false);

onMounted(() => {
    const mindMap = new MindMap({
        el: document.getElementById('mindMapContainer'),
        data: {
            data: {
                text: '根节点'
            },
            children: []
        }
    } as any);
    inited.value = true;
    store.setMindMap(mindMap as any);
    const observer = new ResizeObserver((entries) => {
        for (let entry of entries) {
            const svg = document.querySelector<SVGElement>('#mindMapContainer > svg');
            if (svg) {
                svg.style.width = entry.contentRect.width + 'px';
                svg.style.height = entry.contentRect.height + 'px';
            }
            mindMap.resize();
        }
    });
    observer.observe(document.getElementById('mindMapContainer')!);
    settingsStore.init();
});
</script>

<template>
    <div class="loading" v-loading="loading"></div>
    <div id="mindMapContainer">
        <RichTextToolbar v-if="inited"></RichTextToolbar>
    </div>
    <template v-if="inited">
        <MenuToolbar />
        <SvgContextMenu />
    </template>
</template>
<style scoped lang="less">
.loading {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    :deep(.el-loading-mask) {
        background: rgba(255, 255, 255, 0.5) !important;
    }
}
#mindMapContainer {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;

    * {
        margin: 0;
        padding: 0;
    }
}
</style>
