import Formula from 'simple-mind-map/src/plugins/Formula.js';
import { getChromeVersion } from 'simple-mind-map/src/utils/index.js';
import katex from 'katex';
import Quill from 'quill';
import { bfsWalk } from 'simple-mind-map/src/utils';
import type MindMap from 'simple-mind-map';

class FormulaEx extends Formula {
    declare config: any;
    declare static instanceName: string;
    declare mindMap: MindMap;

    constructor(opt: any) {
        super(opt);
    }

    getStyleText() {
        let css = super.getStyleText();
        css += `
            .katex,.katex-display,.katex-html{
                display: inline-block !important; 
                position: relative;
                font-size: 1em !important;
            }
            .katex-display{
                margin: .1em !important;
            }
            .katex math{
                display: inline-block !important; 
            }
        `;
        return css;
    }

    // 获取katex配置
    getKatexConfig() {
        const config = {
            throwOnError: false,
            errorColor: '#f00',
            output: 'htmlAndMathml', // 默认只输出公式
            displayMode: true
        };
        // Chrome内核100以下，mathml配置公式无法正确渲染
        const chromeVersion = getChromeVersion();
        if (chromeVersion && chromeVersion <= 100) {
            config.output = 'html';
        }
        return config;
    }
    // 修改formula格式工具
    extendQuill() {
        const QuillFormula: any = Quill.import('formats/formula');
        const self = this;
        class CustomFormulaBlot extends QuillFormula {
            static create(value: any) {
                const node: any = super.create(value);
                if (typeof value === 'string') {
                    katex.render(value, node, self.config);
                    node.setAttribute('data-value', value.replaceAll('&', '&amp;')?.replaceAll('<', '&lt;')?.replaceAll('>', '&gt;') ?? '');
                }
                return node;
            }
        }
        Quill.register('formats/formula', CustomFormulaBlot, true);
    }
    latexRichToText(nodeText: string) {
        if (nodeText.indexOf('class="ql-formula"') !== -1) {
            const parser = new DOMParser();
            const doc = parser.parseFromString(nodeText, 'text/html');
            const els = doc.getElementsByClassName('ql-formula');
            for (const el of Array.from(els)) nodeText = nodeText.replace(el.outerHTML, `$${el.getAttribute('data-value')}$`);
        }
        return nodeText;
    }
    checkFormulaIsLegal(str: string) {
        try {
            katex.renderToString(str, this.config);
            return true;
        } catch (e) {
            return false;
        }
    }

    // 修复旧版公式渲染
    repairFormula() {
        const parser = new DOMParser();
        bfsWalk(this.mindMap.renderer.renderTree, (node: any) => {
            if (node?.data?.richText) {
                if (node.data.text) {
                    const doc = parser.parseFromString(node.data.text, 'text/html');
                    const els = doc.getElementsByClassName('ql-formula');
                    for (const el of Array.from(els)) {
                        if (!el.hasAttribute('data-value')) continue;
                        el.setAttribute('data-value', el.getAttribute('data-value')?.replaceAll('<', '&lt;')?.replaceAll('>', '&gt;') ?? '');
                    }
                    node.data.text = doc.body.innerHTML;
                }
            }
        });
    }
}

FormulaEx.instanceName = 'formula';

export default FormulaEx;
